  position: absolute;
  top: 0;
  overflow: hidden;
}

.movie-item.photo .glow {
  width: 40%;
  height: 200%;
  filter: blur(5px);
  background: rgba(255, 255, 255, .2);
  transition: all .5s cubic-bezier(.645, .045, .355, 1);
  display: block;
  position: absolute;
  top: 0;
  transform: rotate(45deg)translate(-450%);
}

.movie-item.photo:hover .glow {
  transition: all 1s cubic-bezier(.645, .045, .355, 1);
  transform: rotate(45deg)translate(450%);
}

.movie-item.photo:hover img, .movie-item.photo:hover .glow-wrap {
