.slider {
  cursor: pointer;
  background-color: #000;
  border-radius: 34px;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slider:before {
  content: "";
  height: 12px;
  width: 12px;
  background-color: var(--button-hover);
  border-radius: 50%;
  transition: all .4s;
  position: absolute;
  bottom: 4px;
  left: 4px;
}

input:checked + .slider {
  background-color: var(--header-text-color);
}

input:checked + .slider:before {
  transform: translateX(14px);
}

